import React from 'react';
import { Introductory } from 'Templates';
import { useLocal } from 'Source/App/Storage/Local';
import { localkey } from 'Source/App/Main/local-config';

export const Introduction = () => {
	const { getLocal } = useLocal(localkey.general);
	const { lightSource } = getLocal();

	return (
		<Introductory
			title='My React Libraries'
			descriptions={[
				'One of the modern react libraries.',
				'Re-inventing the wheel with modern solution.',
				'Build with react hooks.',
			]}
			link='/react/formydable'
			requirement='react >= 16.8.0'
			brandImage={
				<div>
					<img className={`hero-icon ${lightSource}`} alt="react logo" src='/images/tools/react-logo512.png' />
					<img
						className="hero-icon-secondary"
						alt="beard-guy" 
						src='/images/icons/icon-192x192.png'
					/>
				</div>
			}
			columns={[
				{
					title: 'Modern react libraries',
					description: `React is one of the most popular JavaScript front-end frameworks for
					building web applications in ${new Date().getFullYear()}. That is why, React is constantly
					evolving and new features are added constantly like hooks.`,
				},
				{
					title: 'Re-inventing the wheel',
					description: `To reinvent the wheel is to duplicate a basic method that has
					been previously created or optimized by others. which makes
					it new and light-weight with modern solution.`,
				},
				{
					title: 'Build with react hooks',
					description: `Hooks are a new addition in React 16.8.0 They let you use state and
					other React features without writing a class.`,
				},
			]}
		/>
	);
};
