import React from 'react';
import {
	Route,
	RouteNotFound,
	useLocation,
	useHistory,
} from 'reaouter';
import { Sidebar } from 'Templates'; 
import { DocumentationFacade } from 'Source/App/Main/Facades';
import { Formydable, Introduction, Reaouter, Chaperone, Reatable } from './Topics';
import { PageNotFound, ComingSoon } from '../';

const ReactPage = () => {
	const location = useLocation(); 
	const history = useHistory()
	return (
		<>
			<Route path={`${location.path}`} render={<Introduction />} />
			{history.url !== location.url && (
				<>
					<ReactPageSide {...location} />
					<ReactMain {...location} />
				</>
			)}
		</>
	);
};

const ReactPageSide = ({ url }) => {
	const links = [
		{ to: `${url}/formydable`, label: 'formydable' },
		{ to: `${url}/reaouter`, label: 'reaouter' },
		{ to: `${url}/chaperone`, label: 'chaperone' },
		{ to: `${url}/reatable`, label: 'reatable' },
		{ to: `${url}/reactrans`, label: 'reactrans', undev: true },
		{ to: `${url}/reverify`, label: 'reverify', undev: true },
		{ to: `${url}/reanotify`, label: 'reanotify', undev: true },
		{ to: `${url}/selectap`, label: 'selectap', undev: true },
	];
	return <Sidebar links={links} />;
};

const ReactMain = ({ path, url }) => {
	return (
		<DocumentationFacade>
			<Route path={`${path}/formydable`} render={<Formydable />} />
			<Route path={`${path}/reaouter`} exact={false} render={<Reaouter />} />
			<Route path={`${path}/chaperone`} render={<Chaperone />} />
			<Route path={`${path}/reatable`} render={<Reatable />} />
			<Route path={`${path}/reactrans`} render={<ComingSoon />} /> 
			<Route path={`${path}/reverify`} render={<ComingSoon />} /> 
			<Route path={`${path}/reanotify`} render={<ComingSoon />} /> 
			<Route path={`${path}/selectap`} render={<ComingSoon />} />
			<RouteNotFound render={<PageNotFound home={url} />} />
		</DocumentationFacade>
	);
};

export default ReactPage